___CSS_LOADER_EXPORT___.push([module.id, `body {
    font-family: Arial, sans-serif;
    background: linear-gradient(to bottom, #A9CCE3, #E8F6F3);
    color: #333;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    margin: 0;
    padding: 20px;
    box-sizing: border-box;
    transition: background 1s ease-in-out;
}

.main-content {
    background-color: rgba(255, 255, 255, 0.9);
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    width: 100%;
    max-width: 800px;
    text-align: center;
    margin: 20px auto;
    flex: 1;
}

.loading-spinner,
.error-message {
    text-align: center;
    padding: 20px;
    font-size: 1.2em;
    color: #007bff;
    font-weight: bold;
    margin-top: 50px;
}

.error-message {
    color: #dc3545;
}

.title {
    color: #0056b3;
    margin-bottom: 20px;
}

.search-controls {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    margin-bottom: 30px;
}

.search-controls label {
    margin-right: 5px;
    align-self: center;
}

.search-controls input[type="text"],
.search-controls select,
.search-controls button {
    padding: 10px 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1em;
}

.search-controls button {
    background-color: #007bff;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.search-controls button:hover {
    background-color: #0056b3;
}

.current-weather {
    margin-bottom: 40px;
    background-color: #f0f8ff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: inset 0 2px 5px rgba(0, 0, 0, 0.1);
}

.city-name {
    font-size: 2.5em;
    color: #333;
    margin-bottom: 10px;
}

.current-main-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.current-icon {
    width: 80px;
    height: 80px;
    margin-bottom: 10px;
}

.current-temp {
    font-size: 4em;
    font-weight: bold;
    color: #2c3e50;
    margin: 0;
}

.current-description {
    font-size: 1.5em;
    color: #555;
    text-transform: capitalize;
}

.current-details {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    gap: 15px;
    text-align: left;
    font-size: 1em;
    color: #666;
    margin-top: 20px;
}

.detail-item {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 8px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.detail-item:hover {
    background-color: #e6f0ff;
}

.detail-icon {
    width: 24px;
    height: 24px;
}

.current-details p {
    margin: 0;
    font-size: 1em;
}

.current-details span {
    font-weight: bold;
    color: #333;
}

.forecast-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 20px;
    justify-content: center;
}

.day-card {
    background-color: #f7f7f7;
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 15px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    transition: transform 0.2s ease;
}

.day-card:hover {
    transform: translateY(-5px);
}

.day-card img {
    width: 60px;
    height: 60px;
    margin-bottom: 10px;
}

.day-card p {
    margin: 5px 0;
    font-size: 0.95em;
    color: #444;
}

.day-card p:first-child {
    font-weight: bold;
    color: #007bff;
    font-size: 1.1em;
}

.loading-spinner {
    border: 8px solid #f3f3f3;
    border-top: 8px solid #3498db;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    animation: spin 1s linear infinite;
    margin: 50px auto;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@media (max-width: 768px) {
    .main-content { padding: 20px; }
    .city-name { font-size: 2em; }
    .current-temp { font-size: 3em; }
    .current-details { grid-template-columns: 1fr; }
    .search-controls { flex-direction: column; align-items: stretch; }
}

.footer {
    text-align: center;
    padding: 10px;
    font-size: 0.9em;
    color: #777;
    margin-top: auto;
}

.footer a {
    color: #007bff;
    text-decoration: none;
}

.footer a:hover {
    text-decoration: underline;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,8BAA8B;IAC9B,wDAAwD;IACxD,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,iBAAiB;IACjB,SAAS;IACT,aAAa;IACb,sBAAsB;IACtB,qCAAqC;AACzC;;AAEA;IACI,0CAA0C;IAC1C,aAAa;IACb,mBAAmB;IACnB,yCAAyC;IACzC,WAAW;IACX,gBAAgB;IAChB,kBAAkB;IAClB,iBAAiB;IACjB,OAAO;AACX;;AAEA;;IAEI,kBAAkB;IAClB,aAAa;IACb,gBAAgB;IAChB,cAAc;IACd,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,cAAc;IACd,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,uBAAuB;IACvB,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;;;IAGI,kBAAkB;IAClB,sBAAsB;IACtB,kBAAkB;IAClB,cAAc;AAClB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,eAAe;IACf,sCAAsC;AAC1C;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,mBAAmB;IACnB,yBAAyB;IACzB,aAAa;IACb,mBAAmB;IACnB,8CAA8C;AAClD;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,cAAc;IACd,SAAS;AACb;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,2DAA2D;IAC3D,SAAS;IACT,gBAAgB;IAChB,cAAc;IACd,WAAW;IACX,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;IACR,YAAY;IACZ,kBAAkB;IAClB,sCAAsC;AAC1C;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,SAAS;IACT,cAAc;AAClB;;AAEA;IACI,iBAAiB;IACjB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,2DAA2D;IAC3D,SAAS;IACT,uBAAuB;AAC3B;;AAEA;IACI,yBAAyB;IACzB,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;IACb,wCAAwC;IACxC,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,kBAAkB;IAClB,+BAA+B;AACnC;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,cAAc;IACd,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,6BAA6B;IAC7B,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,kCAAkC;IAClC,iBAAiB;AACrB;;AAEA;IACI,KAAK,uBAAuB,EAAE;IAC9B,OAAO,yBAAyB,EAAE;AACtC;;AAEA;IACI,gBAAgB,aAAa,EAAE;IAC/B,aAAa,cAAc,EAAE;IAC7B,gBAAgB,cAAc,EAAE;IAChC,mBAAmB,0BAA0B,EAAE;IAC/C,mBAAmB,sBAAsB,EAAE,oBAAoB,EAAE;AACrE;;AAEA;IACI,kBAAkB;IA